<template>
  <div id="app">
    <Header />
    <section class="container">
      <swiper ref="mySwiper" :options="swiperOption" class="swiper">
        <swiper-slide class="swiper__content content content__detached">
          <Banner />
        </swiper-slide>
        <swiper-slide class="swiper__content content content__detached">
          <Banner />
        </swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>
      <ControlPane @type="changeValue" />
    </section>
    <section class="container container-wrap container-wrap">
      <div class="content content__decore grid-half">
        <Banner />
      </div>
      <div class="content content__house grid-half">
        <Banner />
      </div>
    </section>
    <section v-if="typeOfChart">
      <Chart class="grid-half" :typeOfChart="typeOfChart" :series="series" />
    </section>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import Header from './components/common/Header'
import Banner from './components/common/Banner'
import ControlPane from './components/common/ControlPane'
import Chart from './components/common/Chart'
export default {
  name: 'App',
  components: {
    Header,
    swiper,
    swiperSlide,
    Banner,
    ControlPane,
    Chart
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination'
        }
      },
      typeOfChart: ''
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    },
    series () {
      if (this.typeOfChart === 'pie') return [12, 40, 50, 10, 20]
      else {
        return [{
          name: 'series-1',
          data: [40, 19, 45, 54, 30, 30, 70]
        },
        {
          name: 'series-2',
          data: [12, 19, 5, 14, 70, 20, 70]
        },
        {
          name: 'series-3',
          data: [12, 19, 45, 14, 100, 60, 70, 81]
        }]
      }
    }
  },
  methods: {
    changeValue (value) {
      this.typeOfChart = value
    }
  }
}
</script>

<style lang="scss">
@import "../src/assets/styles/layout/main.scss";
</style>
<style lang="css">
@import 'swiper/dist/css/swiper.css';
</style>



