<template>
  <header class="header">
    <a href="#" class="header__logo">Inform.  Connect.  Inspire.</a>
    <nav :class="['header__navigation', { active: burgerActive }]" @click="toggleMenu">
      <ul class="header-list-secondary">
        <li class="header-list-secondary__item">
          <a href="#">über uns</a>
        </li>
        <li class="header-list-secondary__item">
          <a href="#">Redaktion</a>
        </li>
        <li class="header-list-secondary__item tumbler-local">
          <button
            :class="['btn tumbler-local__button', local === 'de' ? 'active' : '']"
            @click="local = 'de'"
          >
            DE
          </button>
          <button
            :class="['btn tumbler-local__button', local === 'en' ? 'active' : '']"
            @click="local = 'en'"
          >
            EN
          </button>
        </li>
      </ul>
      <ul class="header-list-main">
        <li class="header-list-main__item">
          <label for="header-search" class="label-hidden header__search">
            <input id="header-search" class="input-hidden">
          </label>
        </li>
        <li class="header-list-main__item">
          <a href="#">design+lifestyle</a>
        </li>
        <li class="header-list-main__item">
          <a href="#">trends</a>
        </li>
        <li class="header-list-main__item">
          <a href="#">infodelight</a>
        </li>
        <li class="header-list-main__item">
          <a href="#">smart living</a>
        </li>
      </ul>
    </nav>
    <div class="header__burger-wrap" @click="toggleMenu">
      <button :class="['btn-default burger', { active : burgerActive }]" />
    </div>
  </header>
</template>

<script>

export default {
  name: 'Header',
  data () {
    return {
      local: 'de',
      burgerActive: false
    }
  },
  methods: {
    toggleMenu () {
      if (window.innerWidth <= 1040) {
        this.burgerActive = !this.burgerActive
        if (this.burgerActive) {
          document.body.style.overflow = 'hidden'
          return
        }
        document.body.style.overflow = ''
      }
    }
  }
}
</script>

<style lang="scss">
@import 'header';
</style>



