<template>
  <div class="pane">
    <div class="pane__wrapper">
      <h2 class="pane__heading heading-secondary">A place called Inspiration.</h2>
      <p class="pane__caption text">Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore et dolore magnam voluptatem.</p>
    </div>
    <div class="pane__control">
      <Dropdown class="pane__dropdown" v-model="typeOfChart" :options="options" />
      <button @click="$emit('type', typeOfChart)" class="btn btn-reverse pane__btn">Insert</button>
    </div>
  </div>
</template>

<script>
import Dropdown from '../ui/Dropdown'
export default {
  name: 'ControlPane',
  components: {
    Dropdown
  },
  data () {
    return {
      options: ['pie', 'line', 'bar', 'nothing'],
      typeOfChart: 'nothing'
    }
  }
}
</script>

<style lang="scss">
@import 'control-pane';
</style>



