<template>
  <div class="example" v-if="typeOfChart !== 'nothing'">
    <apexcharts width="100%" height="350" :type="typeOfChart" :options="chartOptions" :series="series"></apexcharts>
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'Chart',
  props: {
    typeOfChart: {
      type: String
    },
    series: {
      type: Array
    }
  },
  components: {
    apexcharts: VueApexCharts
  },
  data () {
    return {
      chartOptions: {
        chart: {
          id: 'basic-bar'
        },
        xaxis: {
          categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998, 1999]
        },
        series: this.series
      }
    }
  }
}
</script>



